import { Component, OnInit } from "@angular/core";
import { ActivatedRoute, Router } from "@angular/router";
import { AppService } from "../shared/app.service";
import { NgxSpinnerService } from "ngx-spinner";
import { AppComponent } from "../app.component";
import { Location } from "@angular/common";
import { UserComponent } from "../model/user/user.component";
import * as moment from "moment";
import * as $ from "jquery";
import * as L from "leaflet";
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
@Component({
  selector: "app-user-teambuilding-detail",
  templateUrl: "./user-teambuilding-detail.component.html",
  styleUrls: ["./user-teambuilding-detail.component.scss"]
})
export class UserTeambuildingDetailComponent extends UserComponent
  implements OnInit {
  public min = new Date(Date.now() - 24 * 60 * 60 * 1000);
  map: any;
  mapvalues: any;
  lat: any;
  lang: any;
  curentlat:any;
  curentlang:any;
  public courseActive = "";
  public selectedPostalCode:any = "";
  public Video: any;
  courseAddress:any;
  public coach_detail = {
    Coach_Fname: "",
    Coach_ID: "",
    Coach_Lname: "",
    Coach_Email: "",
    Coach_Phone: "",
    InstagramURL: "",
    FacebookURL: "",
    TwitterURL: "",
    Coach_Description: "",
    Coach_Experience: "",
    Coach_Rayon: "",
    Coach_Price: "",
    Coach_City: "",
    Coach_Services: "",
    Coach_PriceX10: "",
    Coach_Bank_Name: "",
    Coach_Bank_ACCNum: "",
    Branch_Code: "",
    Coach_Bank_City: "",
    Coach_payment_type: "",
    Coach_transport: "",
    Coach_Image: "../../assets/images/profile_blackwhite.png",
    Coach_Resume: "",
    ResumeName: "",
    Address:"",
  };

  public event_detail = {
    Location: "",
    Postalcode: "",
    Coach_Id: "",
    Description: "",
    Price: "0",
    Photo: "",
    from_date: "",
    to_date: "",
    Eventname: "",
    Eventdetails: "",
    Mode_of_transport: "",
    Plan: "",
    filename: "",
    Address:"",
  };
  public str: any = null;

  public voiture: boolean = false;
  public bus: boolean = false;
  public metro: boolean = false;
  public rer: boolean = false;
  public tram: boolean = false;
  showsuccessMsg:boolean = false;
  reviewform:FormGroup;
  cutomerReviewCollection:any = [];
  count_id:any;
  
  public reserve = {
    Coach_Id: "",
    User_Id: "",
    Course: "",
    Name_of_company: "",
    Email: "",
    Mobile: "",
    Date: "",
    Address: "",
    Postalcode: "",
    Number_of_person: ""
  };

  public selectedCity: any = null;
  IsAllowedCommands:boolean = false;
  ratingOption = 0;
  constructor(
    activatedRoute: ActivatedRoute,
    router: Router,
    appService: AppService,
    location: Location,
    spinner: NgxSpinnerService,
    private formBuilder: FormBuilder
  ) {
    super(activatedRoute, router, appService, location, spinner);
  }
  ngOnInit() {
    this.spinner.show();
    this.reviewform = this.formBuilder.group({
      comments: [null, [Validators.required]]
    });
    this.getReviewDetails();
    var event = JSON.parse(localStorage.getItem("Event"));
    this.event_detail = event;
    var currentUser = JSON.parse(localStorage.getItem("currentUser"));
    this.reserve.Email=currentUser.email;
    this.reserve.Postalcode=currentUser.postalCode;
    var coachevent = {
      P_course: "Teambuilding",
      P_CoachId: event.Coach_Id
    };
    this.appService
      .create("/coachdetail/getcoachbyevent", coachevent)
      .subscribe(async response => {
        if (response && response["data"]) {
          if (response.isSuccess == true) {
            if (response.data.coach_list[0]) {
              this.coach_detail = response.data.coach_list[0];
              var transportData = this.coach_detail.Coach_transport.split(", ");
              this.voiture = transportData.includes("voiture");
              this.bus = transportData.includes("bus");
              this.metro = transportData.includes("métro");
              this.rer = transportData.includes("rer");
              this.tram = transportData.includes("tram");
              this.mapvalues = eval(
                "[" + this.coach_detail["coordonnees_gps"] + "]"
              );
              this.lat = this.mapvalues[0].toFixed(3);
              this.lang = this.mapvalues[1].toFixed(3);
              var temp = new Array();
              temp = this.coach_detail.Coach_payment_type.split(",");
              this.str = temp.join(", ");
              this.spinner.show();
              this.appService
                .getAll("/city/" + this.coach_detail.Coach_City)
                .subscribe(res => {
                  if (res && res["data"]) {
                    if ((res as any).isSuccess == true) {
                      this.selectedCity = (res as any).data.city_list;
                      this.selectedPostalCode = (res as any).data.city_list[0].Code_commune_INSEE;
                      this.spinner.hide();
                    } else {
                      this.spinner.hide();
                    }
                  }
                });

                this.mapintigration(this.mapvalues,this.event_detail.Location,this.event_detail.Address);
              this.spinner.hide();
            } else {
              this.spinner.hide();
            }
          } else {
            this.spinner.hide();
          }
          this.IsAllowedCommands = (response as any).data.review ? (response as any).data.review : false;
          this.allowedCommands();
        }
      });
  }

  allowedCommands(){
    this.IsAllowedCommands ? this.reviewform.enable() : this.reviewform.disable();
  }

  giveRatingOption(star: number){
    if(this.IsAllowedCommands) this.ratingOption = star;
  }

  openURL() {
    window.open(this.Video);
  }
  getReviewDetails() {
    
    var coursedetails = {
      course: "Teambuilding"
    };

    this.appService.getAll("/user/getallreviews",coursedetails).subscribe(response => {

      if(response && response["data"])
      {
        for(var i=0; i < response["data"].length; i++)
        {
         
          this.cutomerReviewCollection.push({
            comments:response["data"][i].comments,
            first_name:response["data"][i].firstName,
            last_name:response["data"][i].lastName,
            user_image:response["data"][i].User_Image,
            today_day: moment(new Date()).format("DD"),
            review_day: moment(response["data"][i].created_at).format("DD"),
            avgrating: response["data"][i].avgrating
          });
         
        }
        this.count_id = response["data"].length;
      }
 
    });

}

  onSubmitComment() {
    
    var Comments = this.reviewform.value.comments;
   
    var event = JSON.parse(localStorage.getItem("Event"));
    var users = JSON.parse(localStorage.getItem("onmytennis"));
    var user = JSON.parse(users);
    var insertComments = {
      Coach_Id: event.Coach_Id,
      User_Id: user.id,
      course: "Teambuilding",
      comments:Comments,
      rating: this.ratingOption
    };
      this.spinner.show();
      this.appService
        .create("/user/createreviews", insertComments)
        .subscribe(
          response => {
            if (response && response.isSuccess == true) {
              this.showsuccessMsg = true;
              setTimeout(()=>{   
                this.showsuccessMsg = false;
        
            location.reload(true);
           }, 1000);
            } 
            else {
              this._showAlertMessage(
                "alert-danger",
                "Échec de l'inscription des entraîneurs"
              );
            }
            this.spinner.hide();
            
          },
          error => {}
        );
    
  }

  download() {
    if (this.coach_detail.Coach_Resume) {
      var blob = this.dataURLtoBlob(this.coach_detail.Coach_Resume);
      var link = document.createElement("a");
      link.href = window.URL.createObjectURL(blob);
      link.download = this.coach_detail.ResumeName;
      link.click();
    }
  }

  dataURLtoBlob(dataurl) {
    var arr = dataurl.split(",");
    var mime = arr[0].match(/:(.*?);/)[1];
    var bstr = window.atob(arr[1]);
    var n = bstr.length;
    var u8arr = new Uint8Array(n);

    while (n--) {
      u8arr[n] = bstr.charCodeAt(n);
    }
    return new Blob([u8arr], {
      type: mime
    });
  }

  onSubmit(reserve) {
    var event = JSON.parse(localStorage.getItem("Event"));
    var users = JSON.parse(localStorage.getItem("onmytennis"));
    var user = JSON.parse(users);
    reserve.Coach_Id = event.Coach_Id;
    reserve.User_Id = user.id;
    reserve.Course = "TeamBuilding";

    var bookArray = {
      Coach_id: event.Coach_Id,
      user_Id: user.id,
      status: "R",
      booking_date: moment(new Date()).format("YYYY-MM-DD"),
      bookingEnd: moment(event.to_date).format("YYYY-MM-DD"),
      course: "TeamBuilding",
      amount: 0,
      P_BookingCourseID:event.id,
      reserve: [reserve]
    };
    this.spinner.show();
    this.appService
      .create("/coachdetail/bookcourse", bookArray)
      .subscribe(response => {
        if (response && response.isSuccess == true) {
          document
            .getElementById("btnbooking")
            .setAttribute("disabled", "true");
          this._showAlertMessage("alert-success", "La demande de team building a été enregistrée avec succès");
          $("#confirmbtn").hide();
          this.spinner.hide();
        } else {
          this._showAlertMessage(
            "alert-danger",
            "La réservation du cours a échoué"
          );
          this.spinner.hide();
        }
      });
  }

  closemodal() {
    this.revokeChanges();
    $("#available").hide();
    $(".modal-backdrop").hide();
    $("body").removeClass("modal-open");
  }

  revokeChanges() {}

  numberOnly(event): boolean {
    if (event.target.value.length > 9) {
      return false;
    }
    const charCode = event.which ? event.which : event.keyCode;
    if (charCode > 31 && (charCode < 48 || charCode > 57)) {
      return false;
    }
    return true;
  }
  mapintigration(mappoint, cityname,address) {
    const BASE_NOMINATIM_URL: string = 'nominatim.openstreetmap.org';
    const DEFAULT_VIEW_BOX: string = 'viewbox=-25.0000%2C70.0000%2C50.0000%2C40.0000';
    let url = `https://${BASE_NOMINATIM_URL}/search?format=json&q=${address}&${DEFAULT_VIEW_BOX}&bounded=1`;
    this.appService.get(url).subscribe((response:any) => {
       console.log(response,'response----Addresss')
       var coordonnees = [];
       if(response.length > 0){
        coordonnees.push(response[0].lat);
        coordonnees.push(response[0].lon);
        this.lat = Number(response[0].lat).toFixed(3);
        this.lang = Number(response[0].lon).toFixed(3);
        this.generatedMap(coordonnees,cityname,address)
       }else{
        this.generatedMap(mappoint,cityname,'');
       }
     },error =>{
      this.generatedMap(mappoint,cityname,'');
    });
    }
  
    generatedMap(mappoint,cityname,address){
      this.map = L.map("map", {
        center: mappoint,
        zoom: 16,
      });
  
      const tiles = L.tileLayer(
        "https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png",
        {
          maxZoom: 25,
        }
      );
  
      tiles.addTo(this.map);
      var greenIcon = L.icon({
        iconUrl: "../assets/images/marker-icon.png",
      });
      L.marker(mappoint, { icon: greenIcon }).addTo(this.map).bindPopup(address ? address : cityname).openPopup();
    }
    mapRerender(mappoint, cityname,address) { 
  
      this.map.remove();
      this.mapintigration(mappoint, cityname,address);
  
    }
  mapurl;
  villeSelected(postal_code: string) {
    this.spinner.show();
    this.appService.getAll("/city/getcitybyID/" + postal_code).subscribe((response) => {
      // tslint:disable-next-line:no-string-literal
      this.spinner.hide();
      if (response && response["data"]) {
        console.log(response);
        var dat = (response as any).data.city_list[0];
        this.mapvalues = eval("[" + dat.coordonnees_gps + "]");
        this.lat = this.mapvalues[0].toFixed(3);
        this.lang = this.mapvalues[1].toFixed(3);
        this.mapurl = `https://www.openstreetmap.org/directions?engine=fossgis_osrm_car&route=${this.curentlat}%2C${this.curentlang}%3B${this.lat}%2C${this.lang}`;
        this.mapRerender(this.mapvalues,dat.Libelle_acheminement,this.event_detail.Address);
      }
    });
  }
  
}
